<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">Link 文字链接</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                文字超链接
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <wl-link >默认链接</wl-link>
                <wl-link type="primary" href="https://www.baidu.com">主要链接</wl-link>
                <wl-link type="success">成功链接</wl-link>
                <wl-link type="warning">警告链接</wl-link>
                <wl-link type="danger">危险链接</wl-link>
                <wl-link type="info">信息链接</wl-link>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                    &lt;wl-link &gt;默认链接&lt;/wl-link&gt;
                    &lt;wl-link type="primary" &gt;主要链接&lt;/wl-link&gt;
                    &lt;wl-link type="success" &gt;成功链接&lt;/wl-link&gt;
                    &lt;wl-link type="warning" &gt;警告链接&lt;/wl-link&gt;
                    &lt;wl-link type="danger" &gt;危险链接&lt;/wl-link&gt;
                    &lt;wl-link type="info" &gt;信息链接&lt;/wl-link&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">禁用状态</div>
            <div class="hb-code-desc">文字链接不可用状态。</div>
            <div class="hb-a">
                <wl-link disabled>默认链接</wl-link>
                <wl-link type="primary" disabled>主要链接</wl-link>
                <wl-link type="success" disabled>成功链接</wl-link>
                <wl-link type="warning" disabled>警告链接</wl-link>
                <wl-link type="danger" disabled>危险链接</wl-link>
                <wl-link type="info" disabled>信息链接</wl-link>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                    &lt;wl-link &gt;默认链接&lt;/wl-link&gt;
                    &lt;wl-link type="primary" disabled &gt;主要链接&lt;/wl-link&gt;
                    &lt;wl-link type="success" disabled &gt;成功链接&lt;/wl-link&gt;
                    &lt;wl-link type="warning" disabled &gt;警告链接&lt;/wl-link&gt;
                    &lt;wl-link type="danger" disabled &gt;危险链接&lt;/wl-link&gt;
                    &lt;wl-link type="info" disabled &gt;信息链接&lt;/wl-link&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">下划线</div>
            <div class="hb-code-desc">文字链接下划线。</div>
            <div class="hb-a">
                <wl-link :underline="false">无下划线</wl-link>
                <wl-link >有下划线</wl-link>
            </div>
            <div class="hb-code">
                <div class="hb-code-desc">通过underline来设置是否有下划线。</div>
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                    &lt;wl-link :underline="false" &gt;无下划线&lt;/wl-link&gt;
                    &lt;wl-link &gt;有下划线&lt;/wl-link&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">图标</div>
            <div class="hb-code-desc">带图标的文字链接可增强辨识度。</div>
            <div class="hb-a">
                <wl-link icon="iconicon-sousuo">搜索</wl-link>
                <wl-link icon="iconicon-lajitong" iconright>删除</wl-link>

            </div>
            <div class="hb-code">
                <div class="hb-code-desc">通过icon属性，来设置图标。图标默认在左侧，iconright 把图标设置在文字的右侧。</div>
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                    &lt;wl-link icon="iconicon-sousuo" &gt;搜索&lt;/wl-link&gt;
                    &lt;wl-link icon="iconicon-lajitong" iconright &gt;删除&lt;/wl-link&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="select"
                            label="可选值">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    import wlLink from '@/components/template/TxtLinkHref'
    export default {
        name: 'wltxtlinkhref',
        data() {
            return {
                tableData: [
                    {
                        param: 'type',
                        desc: '类型',
                        type: 'string',
                        select: 'default / primary / success / warning / danger / info',
                        defaultVal: 'default',
                    },
                    {
                        param: 'disabled',
                        desc: '是否禁用状态',
                        type: 'boolean',
                        select: '--',
                        defaultVal: 'false',
                    },
                    {
                        param: 'underline',
                        desc: '是否显示下划线',
                        type: 'boolean',
                        select: '--',
                        defaultVal: 'true',
                    },
                    {
                        param: 'icon',
                        desc: '是否显示下划线',
                        type: 'string',
                        select: '图标库内所有图标',
                        defaultVal: '--',
                    },
                    {
                        param: 'iconright',
                        desc: '图标在文字右侧',
                        type: 'string',
                        select: '--',
                        defaultVal: '--',
                    }
                ]
            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            wlLink
        }
    }
</script>
<style scoped>

</style>



