<template>
    <a class="wl-link" :class="[type,
                {
                    'is-disabled':disabledBtns,
                    'is-underline':underline,
                    'is-icon':icon!=''
                }
            ]"
            :disabled='disabledBtns'
            :href="href"
            @click="handleClick"
    >
        <i class="iconfont" :class="[icon]" v-show="!iconrights"></i>
        <slot></slot>
        <i class="iconfont" :class="[icon]" v-show="iconrights"></i>
    </a>
</template>
<script>
    export default {
        name: 'txtlinkhref',
        props:{
            type: {
                type: String,
                default: 'default'
            },
            disabled: {
                type:Boolean,
                default:false
            },
            underline:{
                type:Boolean,
                default:true
            },
            icon:{
                type: String,
                default: ''
            },
            iconright:{
                type:Boolean,
                default:false
            },
            href: {
                type: String,
                default: ''
            },
        },
        computed:{
            disabledBtns(){
                let flag = false;
                if(this.disabled){
                    flag = true;
                }else if(!this.disabled || this.disabled === 'false' || this.disabled == ''){
                    flag = false;
                }
                return flag;
            },
            iconrights(){
                let flag = false;
                if(this.iconright){
                    flag = true;
                }else if(!this.iconright || this.iconright === 'false' || this.iconright == ''){
                    flag = false;
                }
                return flag;
            },
        },
        data() {
            return {

            }
        },
        mounted(){
            //console.log(this.iconrights);
        },
        methods:{
            handleClick(evt) {
                this.$emit('click', evt);
            }
        }
    }
</script>
<style scoped>

</style>


